<template>
    <div class="container">
        <div class="lucky-wheel">
            <div class="lucky-title"></div>
            <div class="wheel-main">
                <div class="wheel-pointer-box">
                     <div class="wheel-pointer" @click="rotate_handle()" :style="{transform:rotate_angle_pointer,transition:rotate_transition_pointer}"></div>
                </div>               
                <div class="wheel-bg" :style="{transform:rotate_angle,transition:rotate_transition}">                   
                    <div class="prize-list">
                        <div class="prize-item" v-for="(item,index) in prize_list" :key="index">
                            <div class="prize-pic">
                                <img :src="imgPrefix+item.img">
                            </div>
                            <div class="prize-count" v-if="item.count">
                                {{item.count}}
                            </div>
                            <div class="prize-type">
                                {{item.name}}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="main">
            <div class="main-bg"></div>
            <div class="bg-p"></div>
            <div class="content">
                <div class="lottery-record" @click="show=true"></div>
                <div class="lottery_ticket">{{real_name}},{{lottery_ticket}}</div>
            </div>
            <div class="tip">
                <div class="tip-title"></div>
                <div class="tip-content">
                    <p> 1.14:3012002</p>
                    <p> 2.</p>
                </div>
            </div>
        </div>
        <div class="toast" v-show="toast_control">
            <div class="toast-container">
                <img :src="toast_pictrue" class="toast-picture">
                <div class="close" @click="close_toast()"></div>
                <div class="toast-title">
                    {{toast_title}}
                </div>
                <div class="toast-btn">
                    <div class="toast-cancel"  @click="close_toast"></div>
                </div>
            </div>
        </div>
        <div class="toast-mask" v-show="toast_control"></div>

        <van-popup v-model="show" position="right" class="user-name-put">
            <div class="return-er">
               <button class="return" @click="show=false"></button>
            </div>
            <p style="margin-bottom:1rem;text-align: center;"></p>
<van-pull-refresh v-model="isLoading" @refresh="onRefresh">
          <div v-if=list.length>
          

          <van-list
            v-model="loading"
            :finished="finished"
            @load="onLoad"
          >

          <van-cell  :title="item.commodity_name" v-for="item,listkey in list" :key="listkey" :value="item.created_at">
          </van-cell>
          <br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
          </van-list>
          </div>
          <div v-else>
            <br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
          </div>


          
</van-pull-refresh>
          </van-popup>
    </div>
</template>

<script>
import qs from 'qs';
import { Popup,List,Cell,PullRefresh } from 'vant';
export default {
  components: {
    [Popup.name]:Popup,
    [List.name]:List,
    [Cell.name]:Cell,
    [PullRefresh.name]:PullRefresh,
  },
  data() {
    return {
      listQuery: {
        page: 1,
        limit: 20
      },
      isLoading: false,
      last_page: 0,
      list: [],
      loading: false,
      finished: false,
      show: false,
      real_name: '',
      work_number: '',
      imgPrefix:'https://cdn3.ibingniao.com/',
      easejoy_bean: 0, //
      lottery_ticket: 2, //
      prize_list: [
        {
          icon: require("../assets/img/bean_500.png"), // 
          count: 10, // 
          name: "", // 
          isPrize: 1 // 
        },
        {
          icon: require("../assets/img/bean_five.png"),
          count: 5,
          name: "",
          isPrize: 1
        },
        {
          icon: require("../assets/img/bean_one.png"),
          count: 10,
          name: "",
          isPrize: 1
        },
        {
          icon: require("../assets/img/point_five.png"),
          count: 5,
          name: "",
          isPrize: 1
        },
        {
          icon: require("../assets/img/point_ten.png"),
          count: 10,
          name: "",
          isPrize: 1
        },
        {
          icon: require("../assets/img/bean_500.png"),
          count: 10,
          name: "",
          isPrize: 1
        },
        {
          icon: require("../assets/img/give_up.png"),
          count: 0,
          name: "",
          isPrize: 0
        },
        {
          icon: require("../assets/img/bean_500.png"),
          count: 10,
          name: "",
          isPrize: 1
        }
      ], //
      toast_control: false, //
      hasPrize: true, //
      start_rotating_degree: 0, //
      rotate_angle: 0, //
      start_rotating_degree_pointer: 0, //
      rotate_angle_pointer: 0, //
      rotate_transition: "transform 6s ease-in-out", //
      rotate_transition_pointer: "transform 12s ease-in-out", //
      click_flag: true, //
      i: 0 //
    };
  },
  created() {
    this.$dialog.loading.open('...');
    
    let bn_token = this.getCookie('bn_token');

    if (!this.getCookie('bn_token')) {
        this.$router.push({path:'/login'})
    };
    this.init_prize_list();
    this.UserInfo(bn_token);
  },
  computed: {
    toast_title() {
      return this.hasPrize
        ? "" + this.prize_list[this.i].name
        : "";
    },
    toast_pictrue() {
      return this.hasPrize
        ? require("../assets/img/congratulation.png")
        : require("../assets/img/sorry.png");
    }
  },
  methods: {
        onRefresh() {
            this.listQuery.page = 1;
            this.$axios.get('https://www.310game.com/yh-cms-api/turntable-record?page='+this.listQuery.page+'&limit='+this.listQuery.limit+'&bn_token='+this.getCookie('bn_token')+'&t=' + Date.now())
                          .then((response) => {
                              if(response.status === 200){
                                this.listQuery.page++;
                                this.list = response.data.data;
                                this.isLoading = false;
                                this.finished = false;
                              }else{
                                  this.$dialog.toast({
                                      mes: response.data.message,
                                      timeout: 1500,
                                      icon: 'error'
                                  }); 
                              }
                          }, response => {
                            this.$dialog.loading.close();
                                this.$dialog.toast({
                                    mes: '',
                                    timeout: 1500,
                                    icon: 'error'
                                }); 
                          })
        },
    onLoad() {
          this.$axios.get('https://www.310game.com/yh-cms-api/turntable-record?page='+this.listQuery.page+'&limit='+this.listQuery.limit+'&real_name='+this.real_name+'&work_number='+this.work_number+'&t=' + Date.now())
              .then((response) => {
                  if(response.status === 200){
                    this.last_page = response.data.last_page;
                    if (this.listQuery.page > this.last_page) {
                      this.loading = false;
                        this.finished = true;
                        return ;
                    }else{
                    this.listQuery.page++;
                    this.list = [...this.list, ...response.data.data];
                    this.loading = false;
                    }
                  }else{
                      this.$dialog.toast({
                          mes: response.data.message,
                          timeout: 1500,
                          icon: 'error'
                      }); 
                  }
              }, response => {
                this.$dialog.loading.close();
                    this.$dialog.toast({
                        mes: '',
                        timeout: 1500,
                        icon: 'error'
                    }); 
              })
    },
    //cookie
    setCookie: function (cname, cvalue, exdays) {
        var d = new Date();
        d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
        var expires = "expires=" + d.toUTCString();
        console.info(cname + "=" + cvalue + "; " + expires);
        document.cookie = cname + "=" + cvalue + "; " + expires;
        console.info(document.cookie);
    },
    //cookie
    getCookie: function (cname) {
        var name = cname + "=";
        var ca = document.cookie.split(';');
        for (var i = 0; i < ca.length; i++) {
            var c = ca[i];
            while (c.charAt(0) == ' ') c = c.substring(1);
            if (c.indexOf(name) != -1) return c.substring(name.length, c.length);
        }
        return "";
    },
    //
    init_prize_list(list) {
          this.$axios.get('https://www.310game.com/yh-cms-api/turntable-commodity?t=' + Date.now())
              .then((response) => {
                  if(response.status === 200){
                    this.prize_list = response.data;
                  }else{
                      this.$dialog.toast({
                          mes: response.data.message,
                          timeout: 1500,
                          icon: 'error'
                      }); 
                  }
              }, response => {
                    this.$dialog.toast({
                        mes: '',
                        timeout: 1500,
                        icon: 'error'
                    }); 
              })
    },

    UserInfo(bn_token) {
          this.$axios.get('https://www.310game.com/yh-cms-api/turntable-user-info?t=' + Date.now()+'&bn_token='+bn_token)
              .then((response) => {
                  if(response.status === 200){
                    console.log(response.data.data);
                    this.real_name = response.data.data.TurntableUser.real_name;
                    this.lottery_ticket = response.data.data.lottery_ticket;
                    this.$dialog.loading.close();
                  }else{
                    this.$dialog.loading.close();
                      this.$dialog.toast({
                          mes: response.data.message,
                          timeout: 1500,
                          icon: 'error'
                      }); 
                      this.$router.push({path:'/login'})
                  }
              }, response => {
                this.$dialog.loading.close();
                    this.$dialog.toast({
                        mes: '',
                        timeout: 1500,
                        icon: 'error'
                    }); 
                    this.$router.push({path:'/login'})
              })
    },

    select_prize(){
         this.$dialog.loading.open('...');
          this.$axios.post('https://www.310game.com/yh-cms-api/turntable-commodity-select', qs.stringify({
                  bn_token: this.getCookie('bn_token'),
          }))
              .then((response) => {
                  if(response.status === 200){
                    return response.data.data;
                  }else{
                    this.$dialog.loading.close();
                      this.$dialog.toast({
                          mes: '',
                          timeout: 1500,
                          icon: 'error'
                      }); 
                  }
              }, response => {
                this.$dialog.loading.close();
                    this.$dialog.toast({
                        mes: '',
                        timeout: 1500,
                        icon: 'error'
                    }); 
              })
    },
    rotate_handle() {
       if (!this.click_flag) return;
       this.click_flag = false;
        this.$axios.post('https://www.310game.com/yh-cms-api/turntable-commodity-select', qs.stringify({
                        bn_token: this.getCookie('bn_token'),
                }))
            .then((response) => {
                if(response.status === 200){
                  this.rotating(response.data.data);
                  this.i = response.data.data;
                  
                  if (this.prize_list[this.i].name == '') {
                    this.hasPrize = false;
                  }else{
                    this.hasPrize = true;
                  }
                  this.lottery_ticket = this.lottery_ticket - 1;
                }else{
                  this.$dialog.loading.close();
                    this.$dialog.toast({
                        mes:  response.data.message,
                        timeout: 1500,
                        icon: 'error'
                    }); 
                }
            }, response => {
              this.$dialog.loading.close();
                  this.$dialog.toast({
                      mes: '',
                      timeout: 1500,
                      icon: 'error'
                  }); 
            })
    },
    rotating(index) {
      var type = 0; //  0   1 ()
      var during_time = 5; // 1s
      var random = Math.floor(Math.random() * 7);
      var result_index = index; // prize_list
      var result_angle = [337.5, 292.5, 247.5, 202.5, 157.5, 112.5, 67.5, 22.5]; //
      var rand_circle = 2; // 2-3
      this.click_flag = false; // 
      if (type == 0) {
        // 
        var rotate_angle =
          this.start_rotating_degree +
          rand_circle * 360 +
          result_angle[result_index] -
          this.start_rotating_degree % 360;
          console.log(rotate_angle);
        this.start_rotating_degree = rotate_angle;
        this.rotate_angle = "rotate(" + rotate_angle + "deg)";
        // // //
        // this.rotate_angle_pointer = "rotate("+this.start_rotating_degree_pointer + 360*rand_circle+"deg)";
        // this.start_rotating_degree_pointer =360*rand_circle;
        var that = this;
        // 
        setTimeout(function() {
          that.click_flag = true;
          that.game_over(this.i);
        }, during_time * 1000 + 1500); // 
      } else {
        //
      }
    },
    game_over() {
      this.toast_control = true;
      /*this.hasPrize = this.prize_list[this.i].isPrize*/
    },
    //
    close_toast() {
      this.toast_control = false;
    }
  }
};
</script>

