<template>
<div class="login">
  <div class="welcome"><img src="../assets/welcome.png"></div>
  <div class="login-form">
    <div class="login-inp"><label></label><input v-model="real_name" type="text" placeholder=""></div>
    <div class="login-inp"><label></label><input v-model="work_number" type="text" placeholder=""></div>
    <div class="login-inp"><a @click='fn()'></a></div>
  </div>
</div>
</template>
<style>
    @import './Login.css';

</style>
  
<style>
  .login{width:100%;height:100%;background:url(../assets/login-bg.png) no-repeat;background-size:cover;position:fixed;}
</style>  

<script>
    import qs from 'qs';
    export default {
        data() {
          return {
                  real_name: '',
                  work_number: '',
                  lottery_ticket: '',
          }
        },
        methods: {
            //cookie
            setCookie: function (cname, cvalue, exdays) {
                var d = new Date();
                d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
                var expires = "expires=" + d.toUTCString();
                console.info(cname + "=" + cvalue + "; " + expires);
                document.cookie = cname + "=" + cvalue + "; " + expires;
                console.info(document.cookie);
            },
            //cookie
            getCookie: function (cname) {
                var name = cname + "=";
                var ca = document.cookie.split(';');
                for (var i = 0; i < ca.length; i++) {
                    var c = ca[i];
                    while (c.charAt(0) == ' ') c = c.substring(1);
                    if (c.indexOf(name) != -1) return c.substring(name.length, c.length);
                }
                return "";
            },
            //cookie
            clearCookie: function () {
                this.setCookie("username", "", -1);

            },
            checkCookie: function () {
                var user = this.getCookie("username");
                if (user != "") {
                    alert("Welcome again " + user);
                } else {
                    user = prompt("Please enter your name:", "");
                    if (user != "" && user != null) {
                        this.setCookie("username", user, 365);
                    }
                }
            },
          fn() {
              this.$dialog.loading.open('...');
              this.$axios.post('https://www.310game.com/yh-cms-api/turntable-commodity-login', qs.stringify({
                  real_name: this.real_name,
                  work_number: this.work_number,
              }))
                  .then((response) => {
                      if(response.status === 200){
                        this.setCookie('bn_token',response.data.data,90);
                        this.$router.push({path:'/'})
                      }else{
                        this.$dialog.loading.close();
                          this.$dialog.toast({
                              mes: response.data.message,
                              timeout: 1500,
                              icon: 'error'
                          }); 
                      }
                  }, response => {
                    this.$dialog.loading.close();
                        this.$dialog.toast({
                            mes: '',
                            timeout: 1500,
                            icon: 'error'
                        }); 
                  })
          },
        },
          created() {
                if (this.getCookie('bn_token')) {
                    this.$router.push({path:'/'})
                };

/*              let real_name = this.getCookie('real_name');
              let work_number = this.getCookie('work_number');
              let lottery_ticket = this.getCookie('lottery_ticket');
              if (real_name&&work_number&&lottery_ticket) {
 window.location.href="https://www.310game.com/newcms2/bn-turntable/?from=singlemessage&isappinstalled=0#/?real_name="+real_name+"&work_number="+work_number+"&lottery_ticket="+lottery_ticket+"&v="+Date.now();
              }*/
          },
    }
</script>


